    <div class="container-fluid">
      <a class="navbar-brand" href="#">
        <RouterLink to="/"><img src="../assets/images/illustration/logos/Logo1.png" alt="" width="250">
        </RouterLink>
      </a>
      <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="navbar-collapse collapse" id="navbarSupportedContent">
        <ul class="navbar-nav me-auto mb-2 mb-lg-0">
          <li class="nav-item" @click="close()">
            <a class="nav-link active" aria-current="page" href="#">
              <RouterLink class="nav-link" to="/do">시간 툴
              </RouterLink>
            </a>
          </li>
          <li class="nav-item" @click="close()">
            <a class="nav-link" v-if="checkIfGuest()==true">
              <p class="nav-link">네트워크</p>
            </a>
            <a class="nav-link" v-else>
              <RouterLink class="nav-link" to="/network">네트워크
              </RouterLink>
            </a>
          </li>
          <li class="nav-item" @click="close()">
            <a class="nav-link" v-if="checkIfGuest()==true">
              <p class="nav-link">플랜</p>
            </a>
            <a class="nav-link" v-else>
              <RouterLink class="nav-link" to="/plans">플랜
              </RouterLink>
            </a>
          </li>
        </ul>



        <span class="navbar-text signOut" ref="toMyAccount" v-if="this.$store.state.userLogged==true || !checkIfGuest()"
          style="display:inline;" @click="close()">
          <RouterLink to="/privateProfile" style="display:inline; text-decoration: none; padding: 0px;">
            <div class="logbox myAccount" style="display:inline; ">내 계정</div>
          </RouterLink>
          <span class="slash">&nbsp;&nbsp;/&nbsp;&nbsp;</span>
          <img src="../assets/icones/box-arrow-right.svg" @click="logOut() ,this.$store.commit('setUserIsNotLogged') "
            style="width:1em; display:inline;" alt="join a new circle"
            class="aligncenter text-success filter-green logbox logoutImg">
        </span>
        <span class="navbar-text signIn" ref="toSignIn" v-else @click="close()">
          <RouterLink to="/account" class="nav-link">
            <div class="logbox">로그인</div>
          </RouterLink>
        </span>
      </div>
    </div>
  </nav>
</template>

<script>
import { onMounted, reactive, ref } from 'vue';
import { useRouter } from 'vue-router';


export default {
  computed: {
    complogOut() {
      console.log('I do computed logOut')
      return this.$store.commit('setUserIsNotLogged')
    },
  },
  setup() {
    const router = useRouter();

    const close = () => {
      // alert("닫기")
      document.getElementById('navbarSupportedContent').className = "navbar-collapse collapse";
    }

    const checkIfGuest = () => {
      if (sessionStorage.getItem("guest") == "false") {
        return false
      } else {
        return true
      }
    }
    const logOut = () => {
      console.log("you are logged out")
      sessionStorage.removeItem("guest");
      sessionStorage.removeItem("email");
      sessionStorage.removeItem("TOKEN");
      alert("로그아웃 되었습니다");
      router.push({ name: "home" });
    }
    onMounted(() => {
      checkIfGuest()
    })
    const toSignIn = ref('')
    const toMyAccount = ref('')
    const state = reactive({
      email: sessionStorage.getItem("email"),
      guest: sessionStorage.getItem("guest"),

    })
    return { close, toSignIn, toMyAccount, state, logOut, checkIfGuest }
  }

}
</script>

